import { Component, OnInit, Inject ,ViewChild,ElementRef} from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
//route
import {Router} from '@angular/router'
//service 
import {MembersService} from '../../../members.service'
@Component({
  selector: 'kt-receipt-dialog',
  templateUrl: './receipt-dialog.component.html',
  styleUrls: ['./receipt-dialog.component.scss']
})
export class ReceiptDialogComponent implements OnInit {
  @ViewChild('reportContent') reportContent: ElementRef;
  member
  date=new Date().toDateString();
  constructor(
    private memberservice:MembersService,
    private router:Router,
    public dialogRef: MatDialogRef<ReceiptDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any) { }

  ngOnInit() {
    this.data=this.data['membership']
    let Id=this.router.url.split('/')[5]
    this.memberservice.getMemberById(Id).subscribe(res=>{
      if(res['result'])
      {
        this.member=res['users'][0]
      }
    })
   console.log('data',this.data)
  }


}