import { Component, OnInit, Injectable, ChangeDetectorRef } from '@angular/core';
import { ClubTreeService } from '../../../../../../../core/auth/_services/club-tree.service'
import { MembersService } from '../../../../members.service'
import { NgbDateAdapter, NgbDateStruct, NgbDatepickerConfig } from '@ng-bootstrap/ng-bootstrap';
import { LayoutUtilsService, MessageType, QueryParamsModel } from '../../../../../../../core/_base/crud';
import { Router, ActivatedRoute } from '@angular/router'
import { FormGroup, FormControl, Validators } from '@angular/forms';
import {  FormBuilder } from '@angular/forms'
@Injectable()
export class NgbDateNativeAdapter extends NgbDateAdapter<Date> {
  fromModel(date: Date): NgbDateStruct {
    return (date && date.getFullYear) ? { year: date.getFullYear(), month: date.getMonth() + 1, day: date.getDate() } : null;
  }

  toModel(date: NgbDateStruct): Date {
    return date ? new Date(date.year, date.month - 1, date.day) : null;
  }

}

@Component({
  selector: 'kt-select-membership',
  templateUrl: './select-membership.component.html',
  styleUrls: ['./select-membership.component.scss'],
  providers: [{ provide: NgbDateAdapter, useClass: NgbDateNativeAdapter }]
})
export class SelectMembershipComponent implements OnInit {

  constructor(private clubitemsService: ClubTreeService,
    private memberssevice: MembersService,
    private layoutUtilsService: LayoutUtilsService,
    private route: Router,
    config: NgbDatepickerConfig,
    private router: ActivatedRoute,
    private Changedetectorref: ChangeDetectorRef,
    private fb: FormBuilder) {
      let day=parseFloat(new Date().toISOString().split('T')[0].split('-')[2])
    console.log(day)
      let year=new Date().getFullYear();
      let month=(new Date().getMonth())+1
      config.minDate = { "year":year , "month": month, "day": day} 
    this.member = this.router.parent.snapshot.paramMap.get('id')
    this.packageStatus = this.router.parent.snapshot.queryParamMap.get('status')
    this.membershipandpayment_id = this.router.parent.snapshot.queryParamMap.get('pe')
    this.uk_renewPackage = this.router.snapshot.queryParamMap.get('uk')
    this.end_date = this.router.snapshot.queryParamMap.get('ed')
    this.membership_id=this.router.parent.snapshot.queryParamMap.get('mb')
    this.discount=this.router.snapshot.queryParamMap.get('md')
    this.selectedMembership=this.router.snapshot.queryParamMap.get('type')
  }
 dateform: FormGroup;
  branch;
  end_date;
  membership_id;
  membershipandpayment_id = null;
  nextstep = false
  units;
  submited = false;
  selectedMembership: String;
  selectedunit;
  selectedbranch;
  selected = false;
  selectedValue = ''
  selectedpckage;
  selectedmem = false;
  packages
  member;
  date = new Date()
  discount;
  incorrectDate = false;
  model: Date;
  onlyRequest = true;
  packageStatus;
  uk_renewPackage;
  oldAttedanceStatus;
  renewdate;

  ngOnInit() {
//validator date

    this.clubitemsService.getUnitsData().subscribe(res => {
      if (res['result'])
        this.branch = res['data'];
      //this.selectedValue = res['data'][0]
      if (this.uk_renewPackage) {
        Object.values(this.branch).forEach(function (value) {
          Object.values(value['units']).forEach(function (unit) {
            if (unit['pub_key'] == this.uk_renewPackage) {
              this.selectedValue = value;
              this.selected = true
              this.selectedunit = unit
              this.selectedMembership=this.selectedMembership+" package"
              this.units = value['units']
              this.selectedmem = true
              this.selected_membership()
            }
          }.bind(this));
        }.bind(this))
        this.selected_membership(true)
      }
      this.Changedetectorref.detectChanges();
    })
  }
  selected_unit()
  {
    this.selectedMembership=null;
    this.selectedpckage=null;
    this.packages=null
  }
  selectUnit(item) {
    this.selectedMembership=null;
    this.selectedpckage=null;
    this.packages=null
    this.onlyRequest = true;
    this.selectedbranch = item
    console.log(item)
    if (item['units']) {
      this.selected = true
      this.units = item['units']
      this.selectedunit = item['units'][0]
      console.log(this.units)
    }
    else {
      this.selected = false
    }

  }
  selected_membership(value=true) {
    this.packages=null;
    if(value)
    {
    let downgrade = false
    let upgrade = false
    let membership_id = null
    this.selectedmem = true
    this.discount=null
    console.log(this.selectedMembership)
    if (this.selectedunit && this.selectedMembership.includes('package')) {
      console.log(this.selectedMembership)
      if (this.selectedMembership.includes('package')) {
        if (this.packageStatus == 'downgrade')
          downgrade = true
        if (this.packageStatus == 'upgrade')
          upgrade = true
        if (this.membership_id && (upgrade || downgrade))
          membership_id = this.membership_id

        var units = this.selectedunit['pub_key'];
        var branch = this.selectedValue['pub_key'];
        let type = ((this.selectedMembership.split(' '))[0]).toLocaleLowerCase()

        this.memberssevice.selectedPackages(this.selectedunit['pub_key'], type, downgrade, upgrade, membership_id,units,branch).subscribe(res => {
          value=false;
          if (res['result']) {
            this.packages = res['data']
            console.log(this.packages)
            this.selectedpckage = res['data'][0]

              console.log(this.uk_renewPackage)
              this.packages.forEach(membership=>{
                console.log(membership['_id'],this.membership_id,membership)
                if (membership['_id'] == this.membership_id) {
                  this.selectedpckage = membership
                }
              })
              this.Changedetectorref.detectChanges();
      }
      else this.packages = null
    })
  }
  }
      else if(this.selectedMembership.includes('activity')) {
  this.memberssevice.getAllActivity().subscribe(res => {
    if (res['result']) {
      console.log(this.selectedMembership)
      this.packages = res['data']
      this.Changedetectorref.detectChanges();
      this.Changedetectorref.detectChanges();
      this.memberssevice.changeType('activitiy')
    }
    else this.packages = null
  })
}
      else {
  this.packages = null
}
    }
  }
RenewDate() {
 
  if (this.renewdate == 'default') {
    this.model = this.end_date
  }

}
changeDiscount()
{
  console.log(this.selectedpckage)
this.discount=this.selectedpckage.discount
console.log(this.discount)
this.Changedetectorref.detectChanges();
}
nextStep() {
  this.onlyRequest = true;
  if (this.membershipandpayment_id && this.packageStatus == 'renew') {
    this.RenewPackage()
  }else if(this.packageStatus=='upgrade'){
    this.upgradeanddowngradePackage('upgrade')
  }else if(this.packageStatus=='downgrade'){
    this.upgradeanddowngradePackage('downgrade')
  }
  else {
    this.AddMembership()
  }

}

AddMembership() {
  console.log(this.date,this.model)
  if (this.selectedunit && this.selectedbranch && this.selectedpckage && this.model && this.member) {
      let membership: Object;
      this.memberssevice.DataOfPackage(this.selectedpckage)
      this.memberssevice.changeSelectedMembership({
        'selectedbranch': this.selectedbranch, 'selectedunit': this.selectedunit, 'selectedpckage': this.selectedpckage,
        'units_key': this.selectedunit['pub_key'], 'membership_id': this.selectedpckage['_id'], 'start_date': this.model
      })
      this.memberssevice.currentMembership.subscribe(res => {
        membership = res;
        console.log(res)
      })
      if (membership) {
        membership['pub_key'] = this.member
        membership['membership_id'] = this.selectedpckage['_id'];
        membership['discount'] = this.discount,
          membership['start_date'] = this.model
      }
      else {
        membership = {
          'pub_key': this.member,
          'membership_id': this.selectedpckage['_id'],
          'payment_status': 'pending',
          'discount': this.discount,
          'start_date': this.model,
          'schedual_status': 'pending'
        }

        if (this.selectedMembership.includes('activity')) {
          membership['schedual_status'] = 'activity';
        }
      }
      this.memberssevice.changemembership(membership)
      this.memberssevice.changeSelectedMembership({ 'selectedbranch': this.selectedbranch, 'selectedunit': this.selectedunit, 'selectedpckage': this.selectedpckage, 'units_key': this.selectedunit['pub_key'], 'membership_id': this.selectedpckage['_id'], 'start_date': this.model })

          this.memberssevice.addPackage(membership).subscribe(res => {
            this.onlyRequest = false;
            if (res['result']) {
              this.memberssevice.changeSubmittedMembership(true)
              this.layoutUtilsService.showActionNotification("Membership has successfully added", MessageType.Create);
              this.selectedpckage['se_id'] = res['data']._id;
              if (this.selectedMembership.includes('activity'))
                this.route.navigate(['/default/members/add', this.member, 'schedule', this.selectedpckage['_id']], { queryParams: { iu: this.selectedunit['pub_key'], 'se_id': res['data']._id, 'date': new Date(this.model).getTime(), type: 'activity' } })
              else
                this.route.navigate(['/default/members/add', this.member, 'schedule', this.selectedpckage['_id']], { queryParams: { iu: this.selectedunit['pub_key'], 'se_id': res['data']._id, 'date': new Date(this.model).getTime() } })

              this.memberssevice.changeSubmittedMembership(true)
            }
            else {
              this.layoutUtilsService.showActionNotification("Sorry,Membership has not added try again", MessageType.Create);
            }
          })

      //update

    
  }
  else {
    this.incorrectDate = true
    window.scroll(0,0)
  }


}

onSubmit() {
  this.onlyRequest = true;
  if (this.membershipandpayment_id && this.packageStatus == 'renew') {
    this.RenewPackage()
  }else if(this.packageStatus=='upgrade'){
    this.upgradeanddowngradePackage('upgrade')
  }else if(this.packageStatus=='downgrade'){
    this.upgradeanddowngradePackage('downgrade')
  }
  else {
    this.AddMembership()
  }
}
onAlertClose($event) {
  this.incorrectDate = false;
}
RenewPackage() {
  if (this.renewdate == 'renewDate') {
    this.end_date = new Date(this.model).getTime()
    console.log(this.end_date)
  }
  if ( this.model>= this.end_date) {

    console.log(this.membershipandpayment_id, this.member, this.end_date)
    if (this.membershipandpayment_id && this.member, this.end_date)
      this.memberssevice.renewMebership(this.member, { 'id': this.membershipandpayment_id, 'start_date': this.end_date }).subscribe(res => {

        if (res['result']) {

          this.layoutUtilsService.showActionNotification("Membership successfully is renewed", MessageType.Create);
          this.route.navigate(['/default/members/add', this.member, 'schedule', this.selectedpckage['_id']], { queryParams: { iu: this.uk_renewPackage, 'se_id': res['data']._id, 'date': this.end_date } })
        }
        else {
          this.layoutUtilsService.showActionNotification("Sorry,Membership has not added try again", MessageType.Create);
        }
      })
  }
  else {
    this.incorrectDate = true
    window.scroll(0,0)
  }
}

/** Upgrade Packages **/
upgradeanddowngradePackage(action){
    this.oldAttedanceStatus='cancel';

  if(this.model && this.selectedpckage){
    var membership = {
      'pub_key': this.member,
      'membership_id': this.selectedpckage['_id'],
      'payment_status': 'pending',
      'discount': this.discount,
      'start_date': this.model,
      'privious_membership_id':this.membershipandpayment_id,
      'oldAttedanceStatus':this.oldAttedanceStatus,
      'schedual_status': 'pending'
    }

    if(action=='upgrade'){
      console.log(this.model,this.selectedpckage,action)
      this.memberssevice.upgradePackage(membership).subscribe(res => {
        this.redirectFunction(res,action);
      })
    }else if(action=='downgrade'){
      console.log(this.model,this.selectedpckage,action)
      this.memberssevice.downgradePackage(membership).subscribe(res => {
        this.redirectFunction(res,action);
      })
    }
  }else {
    this.incorrectDate = true
  }
}

redirectFunction(res,action){
  if (res['result']) {
    this.membershipandpayment_id=res['data']._id;
    this.layoutUtilsService.showActionNotification(`Membership successfully is ${action}ed` , MessageType.Create);
    this.route.navigate(['/default/members/add', this.member, 'schedule', this.selectedpckage['_id']], { queryParams: { iu: this.uk_renewPackage, 'se_id': this.membershipandpayment_id, 'date': new Date(this.model).getTime() } })
  }
  else {
    this.layoutUtilsService.showActionNotification(`Sorry,Membership has not ${action}ed try again`, MessageType.Create);
  }
}

}
