import { Component, OnInit ,ChangeDetectorRef} from '@angular/core';
//service
import { MembersService } from '../../members.service'
import { UserService } from '../../../../../core/auth/_services/-user.service'
//formgroup
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
//
import { LayoutUtilsService, MessageType } from '../../../../../core/_base/crud';
//
import { MembershipService } from '../../../../../core/auth/_services'

import {  FileUploader, FileSelectDirective } from 'ng2-file-upload/ng2-file-upload';


import {Router,ActivatedRoute} from '@angular/router'
@Component({
  selector: 'kt-about-member',
  templateUrl: './about-member.component.html',
  styleUrls: ['./about-member.component.scss']
})
export class AboutMemberComponent implements OnInit {
  //** */
  member;
  basicInfoForm: FormGroup
  submitted= false
  error=false
  imageprivew : any;
  confirm : boolean = false;
  image:string=null;

  constructor(private memberservice: MembersService,
     private formBuilder: FormBuilder,
     private userservice:UserService,
     private changDetectRef:ChangeDetectorRef,
     private layoutUtilsService: LayoutUtilsService,
     private route:Router,
     private ser:MembershipService,
     private router:ActivatedRoute) { }

     public uploader: FileUploader = new FileUploader({url: this.ser.upload_file(), itemAlias: 'upload'});


  ngOnInit() {
    this.imageprivew='';
    this.uploader.onAfterAddingFile = (file) => { file.withCredentials = false; };
		this.uploader.onCompleteItem = (item: any, response: any, status: any, headers: any) => {
			var responsePath = JSON.parse(response);
		  if(responsePath.result) this.image = responsePath.image;
    	this.confirm = true;
      this.changDetectRef.detectChanges()
		};

    let id = this.router.parent.snapshot.paramMap.get('id');
    this.memberservice.getMemberById(id).subscribe(res => {
      if(res['result'])
      {
      this.member = res['users'][0]
      this.validData(this.member)
      this.imageprivew=this.member['image_profile'];
      this.changDetectRef.detectChanges()
    }
    })
  }

  validData(member) {
    this.basicInfoForm = this.formBuilder.group({
      name: [member.name, Validators.required],
      username: [member.username, Validators.required],
      email: [member.email, [Validators.required, Validators.email]],
      addressLine: [member.addressLine, Validators.required],
      instagram: [member.instagram],
      twitter: [member.twitter],
      phone: [member.phone, Validators.maxLength(10)],
      emergancyContact: [member.emergancyContact, Validators.maxLength(10)],
      linkedIn: [member. linkedIn, Validators.maxLength(10)],
      medicalCases:[member.medicalCases],
      dateOfBirth:[member.dateOfBirth],
      relationship:[member.relationship],
      gender:[member.gender],
      tagNumber :[member.tagNumber ],
      address:[member.address],
      city:[member. city],
      zipCode:[member.zipCode],
    });
  }
  get valid() { return this.basicInfoForm.controls }
  onSubmit() {

   const controls = this.basicInfoForm.controls;
   /** check form */
   console.log(controls)
   if (this.basicInfoForm.invalid) {
     Object.keys(controls).forEach(controlName =>
     {
       controls[controlName].markAsTouched()
     }
     );

     this.submitted = true;
     return;
   }
  this.userservice.updateUserData({'addressLine':controls['addressLine'].value,
  'email':controls['email'].value,'name':controls['name'].value,
  'username':controls['username'].value,'twitter':controls['twitter'].value,
  'instagram':controls['instagram'].value,'gender':controls['gender'].value,
  'linkedIn':controls['linkedIn'].value,'pub_key':this.member['pub_key'],'image_profile':this.image}).subscribe(res=>{
    if(res['result'])
    {
      this.layoutUtilsService.showActionNotification('member is edited sucessfully', MessageType.Update, 5000, true, true);
      if(this.route.url.includes('profiles')){
        var url = this.route.url.replace("profiles",'profile');
        this.route.navigateByUrl(url);
      }else{
        var url = this.route.url.replace("profile",'profiles');
        this.route.navigateByUrl(url);
      }
    }
    else
    {
     this.error=true
    }
  })
}

handleUpload(event:any) {
this.confirm = false;
if (event.target.files && event.target.files[0]) {
  var reader = new FileReader();
  reader.onload = (event: ProgressEvent) => {
    this.imageprivew = (<FileReader>event.target).result;
    this.changDetectRef.detectChanges()
  }
  reader.readAsDataURL(event.target.files[0]);
}
}

}
