import {Component, OnInit, ViewChild,ChangeDetectorRef} from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {MatPaginator, MatSort, MatTableDataSource} from '@angular/material';

import {merge} from 'rxjs';
import {of as observableOf} from 'rxjs';
import {catchError} from 'rxjs/operators';
import {map} from 'rxjs/operators';
import {startWith} from 'rxjs/operators';
import {switchMap} from 'rxjs/operators';
import { LayoutUtilsService, MessageType, QueryParamsModel } from '../../../../../core/_base/crud';

//service
import {MembersService} from '../../members.service'
@Component({
  selector: 'kt-membership-member',
  templateUrl: './membership-member.component.html',
  styleUrls: ['./membership-member.component.scss']
})
export class MembershipMemberComponent implements OnInit {
  displayedColumns = ['increment','package_name', 'price','start_date','end_date','visits','status'];
  exampleDatabase: MembersService| null;
  dataSource = new MatTableDataSource();
  currentMember;
  resultsLength = 0;
  isLoadingResults = true;
  isRateLimitReached = false;

  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;
  labelPosition;


  constructor(private http: HttpClient,private member:MembersService,
    private layoutUtilsService: LayoutUtilsService,
    private changeDetectorRefs: ChangeDetectorRef) {}

  ngOnInit() {





//current member
this.member.currentmember.subscribe(res=>this.currentMember=res)

    // If the user changes the sort order, reset back to the first page.
	this.sort.sortChange.subscribe(() => this.paginator.pageIndex = 0);
 this.LoadMembership()

  }
  changeLablesPositions(id,event) {

    const _title: string = 'User Delete';
		const _description: string = 'Are you sure to permanently delete this user?';
		const _waitDesciption: string = 'member is updating...';
		const _updateMessage = `member status has been updated`;
    const _failedToUpdate=`sorry there is problem to update this member try again`
		const dialogRef = this.layoutUtilsService.deleteElement(_title, _description, _waitDesciption);
		dialogRef.afterClosed().subscribe(res => {
			if (!res) {
				return;
      }

      if(event['checked'])
      {
        this.member.updateMembership(id,{'status':true}).subscribe(res=>
          {
            if(res['result'])
            {
              this.layoutUtilsService.showActionNotification(_updateMessage, MessageType.Update);
            }
            else{
              this.layoutUtilsService.showActionNotification(_failedToUpdate, MessageType.Update);
            }
          })
      }
      else
      {
        this.member.updateMembership(id,{'status':false}).subscribe(res=>{
          if(res['result'])
          {
            this.layoutUtilsService.showActionNotification(_updateMessage, MessageType.Update);
          }
          else{
            this.layoutUtilsService.showActionNotification(_failedToUpdate, MessageType.Update);
          }
        })
      }
    })

  }
  LoadMembership()
  {
    merge(this.sort.sortChange, this.paginator.page)
    .pipe(
      startWith({}),
    switchMap(() => {
        this.isLoadingResults = true;
      return this.member!.getMembership(this.currentMember['pub_key'],this.paginator.pageIndex+1,3,null,'','','','','',-1);
      }),
      map(res => {
      // Flip flag to show that loading has finished.
        this.isLoadingResults = false;
        this.isRateLimitReached = false;
        console.log(res['data'])
        this.resultsLength = res['data'].length;


        return res
      }),
      catchError(() => {
        this.isLoadingResults = false;

        this.isRateLimitReached = true;
          return observableOf([]);
    })
  ).subscribe(data => {
  if(data['result'])
    this.dataSource.data = data['data']
    this.changeDetectorRefs.detectChanges();
  console.log(data)
  console.log(this.paginator)
  });
  }

}
