import { Component, OnInit,Inject,ViewChild,ElementRef } from '@angular/core';
import {MAT_DIALOG_DATA, MatDialogRef} from "@angular/material";
import { MatPaginator, MatSort, MatSnackBar, MatDialog } from '@angular/material';
// RXJS
import { debounceTime, distinctUntilChanged, tap, skip, delay, take } from 'rxjs/operators';
import { fromEvent, merge, Subscription, of } from 'rxjs';
import {WorkOutService} from '../../../../../../core/auth/_services/workOut.service';
import { SelectionModel } from '@angular/cdk/collections';
import {Category} from '../../../../../../core/auth/_models/category.model'
@Component({
  selector: 'kt-categories',
  templateUrl: './categories.component.html',
  styleUrls: ['./categories.component.scss']
})
export class CategoriesComponent implements OnInit {
  workOuts=[];
  categories;
  URL;
  searchInput
 // @ViewChild('searchInput') searchInput: ElementRef;
  selection = new SelectionModel<Category>(true, []);
 
  private subscriptions: Subscription[] = [];
  constructor(private WorkOutService:WorkOutService,
    private dialogRef: MatDialogRef<CategoriesComponent>,
        @Inject(MAT_DIALOG_DATA) data
    ) {
    }

  ngOnInit() {
    this.getWorkOuts();

  }
  getWorkOuts(){
    let club_key=JSON.parse(localStorage.getItem('user'))['pub_key'];

    this.WorkOutService.getWorkOuts(club_key).subscribe(result =>{
      let categories =result['data']['workout'];
      this.URL=result['data']['url'];
      Object.keys(categories).forEach((elem)=>{
      let obj={key:elem,value:categories[elem]}
      this.workOuts.push(obj)
     console.log(result['data']['workout']);
     })
     console.log(this.workOuts)
    })
  }
  close() {
    this.dialogRef.close();
}
save(){
  console.log(this.selection.selected);
}

}
