import { Component, OnInit ,Inject,ChangeDetectorRef} from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { MatDialog } from '@angular/material';
//
import {ReschedualEditDialogComponent} from '../rechedual-edit.dialog.component'
//
import {Router,ActivatedRoute} from '@angular/router'
//
import {MembersService} from '../../../../members.service'
 @Component({
  selector: 'kt-show-reschdual-time',
  templateUrl: './show-reschdual-time.component.html',
  styleUrls: ['./show-reschdual-time.component.scss']
})
export class ShowReschdualTimeComponent implements OnInit {
ReschdualPackage;
  constructor(public dialogRef: MatDialogRef<ShowReschdualTimeComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any,
    private dialog: MatDialog,
    private router:Router,
    private route:ActivatedRoute,
    private memberservice:MembersService,
    private changeDetectRef:ChangeDetectorRef) {
      this.dialogRef.disableClose = true;
      this.Reschedual_items=this.data;

    }
    color=["kt-widget2__item kt-widget2__item--warning",
    'kt-widget2__item kt-widget2__item--danger',
    'kt-widget2__item kt-widget2__item--info',
    'kt-widget2__item kt-widget2__item--success'
    ]
    units_key;
    membership_id;
    packageId;
    Reschedual_items;
    viewLoading=true;
  ngOnInit() {

    this.memberservice._savedReschdualPackage.subscribe(res=>{
     this.ReschdualPackage=res;
   })
    this.data.forEach((element,index) => {
      if(element['from'])
      {
        this.units_key=element.units_key;
        console.log(element)
        this.membership_id=element.membershipandpayment_id
        this.packageId=element.membership_id
        element['from']=`${(new Date(element['from'])).getHours()}:${(new Date(element['from'])).getMinutes()}`
      }
      if(element['to'])
      {
        element['to']=`${(new Date(element['to'])).getHours()}:${(new Date(element['to'])).getMinutes()}`
      }
       element['color']=this.color[index%4]
    console.log(this.color[index%4])
    });

  }
  reschedualcancel()
  {
   
    this.dialogRef.close();
    this.dialog.open(ReschedualEditDialogComponent,{data:this.ReschdualPackage})
    this.changeDetectRef.detectChanges();
  }
  reschedualshow()
  {
   //this.route.parent.snapshot.paramMap.get('id');
   let  reschedual_package;
   this.memberservice._savedReschdualPackage.subscribe(res => {
    reschedual_package = res;
    reschedual_package = JSON.parse(reschedual_package);
    reschedual_package['number'] = this.data.length
    reschedual_package = JSON.stringify(reschedual_package)
  })
  this.memberservice.saveReschdualPackage(reschedual_package)
  let  split=this.router.url.split('/')
  let user_key=split[5]
    console.log(user_key,this.membership_id,this.units_key,this.Reschedual_items)
    
    this.memberservice.changeSelectedMembership(this.Reschedual_items)
   if(user_key&&this.membership_id&&this.units_key)
    this.router.navigate(['/default/members/list/profile',user_key,'ReschduelAndcancel',this.membership_id],{queryParams:{'uk':this.units_key,'piD':this.packageId}})
    this.dialogRef.close()

  }
  cancel()
  {
    this.dialogRef.close()
  }
}
