import { Component, OnInit, Input, ChangeDetectorRef } from '@angular/core';
import { MembersService } from '../members.service'
import { Router } from '@angular/router'
import { debounceTime, distinctUntilChanged, map } from 'rxjs/operators';
export interface Widget5Data {
  pic?: string;
  username: string;
  name: string;
  email?: string;
  info?: string;
  status?: string;
  largeInfo?: string;
}
@Component({
  selector: 'kt-member-list',
  templateUrl: './member-list.component.html',
  styleUrls: ['./member-list.component.scss']
})
export class MemberListComponent implements OnInit {
  SearchKey:string='';
	filterStatus: string = '';
  filterType: string = '';
  filterGender: string = '';
	filterPayment: string = '';
  orderby: string = '';
  order : number = 1;
  mode = 'indeterminate';
  value = 50;
  diameter = 50;
  color = 'primary';
  displayProgressSpinner: boolean;
  displayProgressSpinnerInBlock: boolean = true;
  getalltypes = [{key:'Private',value:'private'},{key:'General',value:'general'},{key:'Activities',value:'activities'}]
  getallpayment = [{key:'Made',value:'complete'},{key:'Didn\'t make',value:'false'},{key:'Pending',value:'pending'}]
  oderitems = [{key:'Latest',value:'id'},{key:'Name',value:'name'},{key:'Email',value:'email'},{key:'Gender',value:'gender'},{key:'Type',value:'type'},{key:'Status',value:'status'}]

  constructor(private memberservice: MembersService,
    private router: Router,
    private changeDetectRef: ChangeDetectorRef
  ) {

  }
  @Input() data: Widget5Data[];
  search = 'search...';
  /**
   * @ Lifecycle sequences => https://angular.io/guide/lifecycle-hooks
   */

  /**
   * On init
   */
  ngOnInit() {
    this.getMember();
    this.memberservice.changeSelectedMembership(null);
    this.memberservice.changeSubmittedMembership(false);
    this.memberservice.changemembership(null)
    // this.memberservice.changeuser(null)

  }

  onSearchClear()
    {
		this.SearchKey=""
		this.applyFilter()
	}

  /*
  *
  *
  */
  //method to  search online
  applyFilter(orderby=''){
    if(this.SearchKey && this.SearchKey.length > 0)
      var search = this.SearchKey.trim().toLowerCase();
    else var search='';
    if (this.filterStatus && this.filterStatus.length > 0)
      var status = this.filterStatus;
    else status = '';

    if (this.filterType && this.filterType.length > 0)
      var type = this.filterType;
    else type = '';

    if (this.filterGender && this.filterGender.length > 0)
      var gender = this.filterGender;
    else gender = '';

    if (this.filterPayment && this.filterPayment.length > 0)
      var payment = this.filterPayment;
    else payment = '';

    this.getMember(null,null,search,status,type,gender,payment,this.orderby,this.order);
  }

  getMember(pages = 1, prepage = 10, search = null,status='',type='',gender='',payment='',orderby='',order=-1) {
    this.memberservice.getAllMembers(pages, prepage, search,status,type,gender,payment,orderby,order).subscribe(res => {
      if (res['result']){
        this.data = res['users']
      this.changeDetectRef.detectChanges();
     }else this.data = [];
    })

  }
  nextPage(event) {
    console.log(event)
    this.getMember(event.pageIndex + 1, event.pageSize, null);
  }
  // routeToprofile(member) {
  //   this.memberservice.changeuser(member)
  // }
  searchWord() {
    console.log(this.search)
    this.memberservice.getAllMembers(null, null, this.search).subscribe(res => {
      if (res['result'])
        this.data = res['users']
      console.log(this.data)
      this.changeDetectRef.detectChanges();
    })
  }
}
