// Angular
import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule, Routes } from '@angular/router';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { HttpClientModule, HTTP_INTERCEPTORS } from '@angular/common/http';
// NGRX
import { StoreModule } from '@ngrx/store';
import { EffectsModule } from '@ngrx/effects';
// Translate
import { TranslateModule } from '@ngx-translate/core';
import { PartialsModule } from '../../partials/partials.module';
// Services
import { HttpUtilsService, TypesUtilsService, InterceptService, LayoutUtilsService} from '../../../core/_base/crud';
// Shared
import { ActionNotificationComponent } from '../../partials/content/crud';
// Components
import {MembersComponent} from './members.component'
import { NgbModule } from '@ng-bootstrap/ng-bootstrap'
//
import {PermissionGuard} from '../../../core/auth/_guards/permission.guard'
//core
import { CoreModule } from '../../../core/core.module';
//import { FileUploadModule } from '../../../../../node_modules/ng2-file-upload/file-upload/file-upload.module.d';
import { SharedModule } from '../sharing.module';
// Material
import {
	MatExpansionPanel,
	MatToolbarModule,
	MatSidenavModule,
	MatGridListModule,MatDividerModule, MatListModule ,
	MatInputModule,
	MatPaginatorModule,
	MatProgressSpinnerModule,
	MatSortModule,
	MatTableModule,
	MatSelectModule,
	MatMenuModule,
	MatProgressBarModule,
	MatButtonModule,
	MatCheckboxModule,
	MatDialogModule,
	MatTabsModule,
	MatNativeDateModule,
	MatCardModule,
	MatRadioModule,
	MatIconModule,
	MatDatepickerModule,
	MatAutocompleteModule,
	MAT_DIALOG_DEFAULT_OPTIONS,
	MatSnackBarModule,
	MatTooltipModule,

	MatSlideToggleModule
} from '@angular/material';
import {
	usersReducer,
	UserEffects
} from '../../../core/auth';
import {MatExpansionModule} from '@angular/material/expansion';
//calender
import { FlatpickrModule } from 'angularx-flatpickr';
import { CalendarModule, DateAdapter } from 'angular-calendar';
import { adapterFactory } from 'angular-calendar/date-adapters/date-fns';
import { NgbModalModule } from '@ng-bootstrap/ng-bootstrap';
import { ReschedualEditDialogComponent } from './member-profile/reschedule/rechedual-edit/rechedual-edit.dialog.component';
//childern component
import {MemberProfileComponent} from './member-profile/member-profile.component'
import {MemberListComponent} from './member-list/member-list.component'
import { AboutMemberComponent } from './member-profile/about-member/about-member.component';
import { MembershipMemberComponent } from './member-profile/membership-member/membership-member.component';
import { PaymentsMemberComponent } from './member-profile/payments-member/payments-member.component';
import { AgreementsMemberComponent } from './member-profile/agreements-member/agreements-member.component';
import {ReschedualComponent} from './member-profile/reschedule/reschedule.component'
import { AddMembershipComponent } from './member-profile/membership-member/add-membership/add-membership.component';
import { MembershipListComponent } from './member-profile/membership-member/membership-list/membership-list.component';
import { SelectMembershipComponent } from './member-profile/membership-member/add-membership/select-membership/select-membership.component';
import { ScheduleComponent } from './member-profile/membership-member/add-membership/schedule/schedule.component';
import { PaymentComponent } from './member-profile/membership-member/add-membership/payment/payment.component';
import {MyPageModule} from '../my-page/my-page.module';
import { ReceiptDialogComponent } from './member-profile/payments-member/receipt-dialog/receipt-dialog.component';
import { ShowReschdualTimeComponent } from './member-profile/reschedule/rechedual-edit/show-reschdual-time/show-reschdual-time.component';
import { ReschdualAndcancelComponent } from './member-profile/reschedule/reschdual-andcancel/reschdual-andcancel.component';
import {WorkoutComponent} from './member-profile/workout/workout.component';
import { CategoriesComponent } from './member-profile/workout/categories/categories.component'

import { FileUploadModule } from 'ng2-file-upload';
const routes: Routes = [
	{
		path: '',
		component: MembersComponent,
		children:[
			{
			path: '',
			redirectTo: 'list',
			pathMatch: 'full',
			},
			{
				path:'list/profile/:id',
				component:MemberProfileComponent,
				data:{title:'memberprofile'},children:
				[{path:'about',
				component:AboutMemberComponent,
				data:{title:'aboutprofile'}},
				{
					path:'memership',
					component:MembershipMemberComponent,
					data:{title:'memership'},
					children:[{path:'list/:pub_key',component:MembershipListComponent},{path:'list',component:MembershipListComponent}]
				},
				{
					path:'payment/:pub_key',
					component:PaymentsMemberComponent,
					data:{title:'memership'}
				},
				{
					path:'payment',
					component:PaymentsMemberComponent,
					data:{title:'memership'}
				},
				{
					path:'agreement',
					component:AgreementsMemberComponent,
					data:{title:'memership-member'}
				},
				{
					path:'workout',
					component:WorkoutComponent,
					data:{title:'memership-member'},
					children:[]
				},
				{
					path:'reschedule/:pub_key',
					component:ReschedualComponent,
					data:{title:'memership-member'},
					children:[]
				},
				{
					path:'reschedules',
					component:ReschedualComponent,
					data:{title:'memership-member'},
					children:[]
				},
				{
					path:'reschedule',
					component:ReschedualComponent,
					data:{title:'memership-member'},
					children:[]
				},

				{path:'ReschduelAndcancel/:id',
				component:ReschdualAndcancelComponent}
			     ]
			},
			{
				path:'list/profiles/:id',
				component:MemberProfileComponent,
				data:{title:'memberprofile'},children:
				[{path:'about',
				component:AboutMemberComponent,
				data:{title:'aboutprofile'}},
				{
					path:'memership',
					component:MembershipMemberComponent,
					data:{title:'memership'},
					children:[{path:'list/:pub_key',component:MembershipListComponent},{path:'list',component:MembershipListComponent}]
				},
				{
					path:'payment/:pub_key',
					component:PaymentsMemberComponent,
					data:{title:'memership'}
				},
				{
					path:'payment',
					component:PaymentsMemberComponent,
					data:{title:'memership'}
				},
				{
					path:'agreement',
					component:AgreementsMemberComponent,
					data:{title:'memership-member'}
				},
				{
					path:'reschedule/:pub_key',
					component:ReschedualComponent,
					data:{title:'memership-member'},
					children:[]
				},
				{
					path:'reschedule',
					component:ReschedualComponent,
					data:{title:'memership-member'},
					children:[]
				},
				
	
				{path:'ReschduelAndcancel/:id',
				component:ReschdualAndcancelComponent}
			     ]
			},
			{
				path:'list',
				component:MemberListComponent,
				data:{title:'memberlist'}
			},
			{path:'add/:id',component:AddMembershipComponent,children:[
				{path:'select',component:SelectMembershipComponent},
				{path:'select/:id',component:SelectMembershipComponent},
				{path:'schedule',component:ScheduleComponent},
				{path:'schedule/:id',component:ScheduleComponent},
				{path:'payment-cart',component:PaymentComponent},
				{path:'payment-cart/:id',component:PaymentComponent},
			]},
			{

			}
		]
	}
];

@NgModule({
	imports: [
		NgbModule,
		MatGridListModule,MatDividerModule, MatListModule ,MatSidenavModule,MatToolbarModule,MatSlideToggleModule,
		CommonModule,
		HttpClientModule,
		SharedModule,
		PartialsModule,
		RouterModule.forChild(routes),
		StoreModule.forFeature('users', usersReducer),
        EffectsModule.forFeature([UserEffects]),
		FormsModule,
		ReactiveFormsModule,
		TranslateModule.forChild(),
		MatButtonModule,
		MatMenuModule,
		MatSelectModule,
        MatInputModule,
		MatTableModule,
	    MatExpansionModule,
		MatAutocompleteModule,
		MatRadioModule,
		MatIconModule,
		MatNativeDateModule,
		MatProgressBarModule,
		MatDatepickerModule,
		MatCardModule,
		MatPaginatorModule,
		MatSortModule,
		MatCheckboxModule,
		MatProgressSpinnerModule,
		MatSnackBarModule,
		MatExpansionModule,
		MatTabsModule,
		MatTooltipModule,
		MatDialogModule,
		MyPageModule,
		FlatpickrModule.forRoot(),
		CalendarModule.forRoot({
		  provide: DateAdapter,
		  useFactory: adapterFactory}),
	
		// FileUploadModule

	],
	providers: [
		InterceptService,
		{
        	provide: HTTP_INTERCEPTORS,
       	 	useClass: InterceptService,
			multi: true
		},
		{
			provide: MAT_DIALOG_DEFAULT_OPTIONS,
			useValue: {
				hasBackdrop: true,
				panelClass: 'kt-mat-dialog-container__wrapper',
				height: 'auto',
				width: '900px'
			}
		},
		HttpUtilsService,
		TypesUtilsService,
		LayoutUtilsService,
		PermissionGuard
	],
	entryComponents: [
		ReceiptDialogComponent,
		ActionNotificationComponent,
	    ReschedualEditDialogComponent,
		ShowReschdualTimeComponent,
		CategoriesComponent
	],
	declarations: [
		ReschedualEditDialogComponent,
		MembersComponent,
		MemberProfileComponent,
		MemberListComponent,
		AboutMemberComponent,
		MembershipMemberComponent,
		PaymentsMemberComponent,
		AgreementsMemberComponent,
	    ReschedualComponent,
		AddMembershipComponent,
		MembershipListComponent,
		SelectMembershipComponent,
		ScheduleComponent,
		PaymentComponent,
		ReceiptDialogComponent,
		ReschedualEditDialogComponent,
		ShowReschdualTimeComponent,
		ReschdualAndcancelComponent,
		WorkoutComponent,
		CategoriesComponent
        
	]
})
export class MembersModule {}
