// Angular
import { Component, OnInit, Inject, ChangeDetectionStrategy, OnDestroy } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { ShowReschdualTimeComponent } from './show-reschdual-time/show-reschdual-time.component'
//import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatDialog } from '@angular/material';
// Lodash
import { Router } from '@angular/router';
import * as _lodash from 'lodash';
// RxJS
import { Observable, of, Subscription } from 'rxjs';
// Lodash
//import { each, find, some } from 'lodash';
// NGRX
//import { Update } from '@ngrx/entity';
import { Store, select } from '@ngrx/store';
// State
import { AppState } from '../../../../../../core/reducers';
import { LayoutUtilsService, MessageType, QueryParamsModel } from '../../../../../../core/_base/crud';
//import { delay } from 'rxjs/operators';
//import { Reschedual } from './rechedual.model';
//service
import { MembersService } from '../../../members.service'
//import {  FileUploader, FileSelectDirective } from 'ng2-file-upload/ng2-file-upload';
@Component({
	selector: 'kt-rechedual-edit-dialog',
	templateUrl: './rechedual-edit.dialog.component.html',
	styleUrls: ['../reschedule.component.scss'],
	changeDetection: ChangeDetectionStrategy.Default,
})
export class ReschedualEditDialogComponent implements OnInit, OnDestroy {
	// Public properties



	// Private properties
	private componentSubscriptions: Subscription;

	/**
	 * Component constructor
	 *
	 * @param dialogRef: MatDialogRef<ReschedualEditDialogComponent>
	 * @param data: any
	 * @param store: Store<AppState>
	 */
	constructor(public dialogRef: MatDialogRef<ReschedualEditDialogComponent>,
		private layoutUtilsService: LayoutUtilsService,
		@Inject(MAT_DIALOG_DATA) public data: any, private dialog: MatDialog,
		private store: Store<AppState>, private router: Router, private Memberservice: MembersService) { }
	startdate;
	enddate;
	value;
	viewLoading=true;
	loadingAfterSubmit=true;
	selectedTab=0;
	submitted = false;
	membershipandpaymentData;
	dateNow = new Date();
	disabled = true
	/**
	 * @ Lifecycle sequences => https://angular.io/guide/lifecycle-hooks
	 */

	/**
	 * On init
	 */
	ngOnInit() {
		console.log('data', this.data)
		this.data = JSON.parse(`${this.data}`)
		console.log('data', this.data)
		this.Memberservice.getSelectedPackageById(this.data['membership_id']).subscribe(res=>{
			
			if(res['result'])
			{
				this.membershipandpaymentData=res['data'][0]
				console.log(this.membershipandpaymentData)
			}
		})
	
	
		
	}


	/**
	 * On destroy
	 */
	ngOnDestroy() {
		if (this.componentSubscriptions) {
			this.componentSubscriptions.unsubscribe();
		}
	}
	cancelAttendance(value) {
		let data = {}
		if (this.data['from'])
			data['from'] = `${(new Date(this.data['from'])).getHours()}:${(new Date(this.data['from'])).getMinutes()}`
		if (this.data['to'])
			data['to'] = `${(new Date(this.data['to'])).getHours()}:${(new Date(this.data['to'])).getMinutes()}`
		data['id'] = this.data['_idAttendance']
		const _title: string = 'Cancel Attendance';
		const _description: string = 'Are you sure to cancel this attendance?';
		const _waitDesciption: string = 'Attendance is canceling...';
		const _deleteMessage = `Attendance has been canceled`;
		const _failedToDelete = `sorry there is problem to  cancel try again`
		if (this.data['_idAttendance']) {
			const dialogRef = this.layoutUtilsService.deleteElement(_title, _description, _waitDesciption);
			dialogRef.afterClosed().subscribe(res => {
				if (!res) {
					return;
				}
				this.Memberservice.cancelAttendance(data).subscribe(res => {

					if (res['result']) {

						this.layoutUtilsService.showActionNotification(_deleteMessage, MessageType.Delete);
						this.dialogRef.close();
						let member_id=this.router.url.split('/')[5]
						if(this.router.url.split('/')[7]=='reschedules')
						{
							this.router.navigate(['/default/members/list/profile',member_id,"reschedule"])
						}
						else{
							this.router.navigate(['/default/members/list/profile',member_id,"reschedules"])

						}
					}
					else {
						this.layoutUtilsService.showActionNotification(_failedToDelete, MessageType.Delete);
					}
				})
			})

		}
	}

	/**
	 * Save data
	 */

	/**
	 * Close alert
	 *
	 * @param $event: Event
	 */


	/** UI */
	/**
	 * Returns component title
	 */
	getTitle(): string {
		return 'New Reschedual';
	}

	isTitleValid(): boolean {
		return true;
	}
	reschedualshow() {
		this.data['status']='active'
		let reschedual_package;
		if (this.value && this.disabled) {
			if (this.value == 'all') {
				this.Memberservice._savedReschdualPackage.subscribe(res => {
					reschedual_package = res;
					reschedual_package = JSON.parse(reschedual_package)
				  reschedual_package['endoldday']=new Date(this.membershipandpaymentData['end_date']) .toISOString().split('T')[0]
					console.log(reschedual_package['endoldday'])
					reschedual_package = JSON.stringify(reschedual_package)
				})
				this.Memberservice.saveReschdualPackage(reschedual_package)
				this.data['day_from'] = this.data['day'];
				delete this.data['_idAttendance']
				this.Memberservice.viewAttendance(this.data).subscribe(res => {
					if (res['result']) {
						this.disabled = false;
						
						this.dialogRef.close();
						this.dialog.open(ShowReschdualTimeComponent, { data: res['data'] });
					}
				})
			}
			else if (this.value == 'period') {
				if (this.enddate) {
					this.Memberservice._savedReschdualPackage.subscribe(res => {
						reschedual_package = res;
						reschedual_package = JSON.parse(reschedual_package)
						reschedual_package['endoldday'] = new Date ((new Date(this.enddate).getTime())+(60*60*24*1000)).toISOString().split('T')[0];
						console.log(reschedual_package['endoldday'])
						reschedual_package = JSON.stringify(reschedual_package)
					})
					this.Memberservice.saveReschdualPackage(reschedual_package)
					this.submitted = false;
				 	console.log(this.enddate, this.data['day'])
					this.data['day_from'] = this.data['day']
					this.data['day_to'] = new Date ((new Date(this.enddate).getTime())+(60*60*24*1000)).toISOString().split('T')[0];
					console.log(this.data['day_to'], this.data['day_from'])
					delete this.data['_idAttendance']
					this.Memberservice.viewAttendance(this.data).subscribe(res => {
						if (res['result']) {
							this.disabled = false;
					 		this.dialogRef.close();
							this.dialog.open(ShowReschdualTimeComponent, { data: res['data'] });
							console.log(res)
						}
					})
				}
				else {
					this.submitted = true;
				}
			}
			else if (this.value == 'one') {
				console.log(this.data)
				this.data['id'] = this.data['_idAttendance']
				this.data['day_from'] = this.data['day'];
				this.data['day_to'] = this.data['day'];
				this.Memberservice._savedReschdualPackage.subscribe(res => {
					reschedual_package = res;
					reschedual_package = JSON.parse(reschedual_package);
					reschedual_package['endoldday'] = this.data['day']
					reschedual_package['_idAttendance']=this.data['_idAttendance']
					reschedual_package = JSON.stringify(reschedual_package)
				})
				this.Memberservice.saveReschdualPackage(reschedual_package)
				this.Memberservice.viewAttendance(this.data).subscribe(res => {
					if (res['result']) {
						this.disabled = false;
						this.dialogRef.close();
						res['data']['_idAttendance']=this.data['_idAttendance']
						this.dialog.open(ShowReschdualTimeComponent, { data: res['data'] });
						console.log(res)
					}
				})

			}
		}
	}
	reschedual(activeClass) {
		this.value = activeClass
	}
	cancel(){
		
	}
}
