import { Component, OnInit, ViewChild, ChangeDetectorRef } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { MatPaginator, MatSort, MatTableDataSource } from '@angular/material';
import { Observable } from 'rxjs';
import { merge } from 'rxjs';
import { of as observableOf } from 'rxjs';
import { catchError } from 'rxjs/operators';
import { startWith } from 'rxjs/operators';
import { switchMap } from 'rxjs/operators';
import { Router,ActivatedRoute } from '@angular/router'
import { LayoutUtilsService, MessageType, QueryParamsModel } from '../../../../../../core/_base/crud';
import { debounceTime, distinctUntilChanged, map } from 'rxjs/operators';
import { NgbDropdownConfig } from '@ng-bootstrap/ng-bootstrap';
//service
import { MembersService } from '../../../members.service'

@Component({
  selector: 'kt-membership-list',
  templateUrl: './membership-list.component.html',
  styleUrls: ['./membership-list.component.scss']
})
export class MembershipListComponent implements OnInit {
  displayedColumnMembership = ['actions','increment', 'package_name', 'price', 'start_date', 'end_date', 'payment_status', 'visits','schedual_status'];
  displayedColumnBenfits = ['increment', 'additional_invitation', 'additional_visits', 'additional_service'];
  exampleDatabase: MembersService | null;
  dataSource = new MatTableDataSource();
  currentMember;
  resultsLength = 0;
  isLoadingResults = true;
  isRateLimitReached = false;
  pub_key: string ='';
  SearchKey:string='';
	filterStatus: string = '';
	filterPayment: string = '';
  filterSchedual: string = '';
  orderby: string = '';
  order : number = -1;
  filterids:string='';
  getallpayment = [{key:'Compelete',value:'complete'},{key:'Pending',value:'pending'}]
  getallschedual = [{key:'Compelete',value:'complete'},{key:'Pending',value:'pending'}]
  oderitems = [{key:'Latest',value:'id'},{key:'Name',value:'name'},{key:'Price',value:'price'},{key:'Start date',value:'start_date'},{key:'End date',value:'end_date'},{key:'Type',value:'type'},{key:'Status',value:'status'}
              ,{key:'Visits',value:'visits'},{key:'Payment status',value:'payment_status'},{key:'Schedual status',value:'schedual_status'}]

  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;
  labelPosition;
 length;
 pagesize=10;
 ScheduleStatus:string=''
 member_id:string;
  public model: any;
  constructor(private http: HttpClient, private member: MembersService,
    private layoutUtilsService: LayoutUtilsService,
    private changeDetectorRefs: ChangeDetectorRef,
    config: NgbDropdownConfig,
    private route:ActivatedRoute,
    private router: Router) {
    config.placement = 'top-right';
    config.autoClose = false;
    this.pub_key =(this.router.url.split('/'))[5]
    this.ScheduleStatus=this.route.snapshot.queryParamMap.get('status')
    this.filterids = this.route.snapshot.queryParamMap.get('ids')
    if(this.filterids == null) this.filterids='';
  }
  ngOnInit() {
   if(typeof this.ScheduleStatus=='string'&&this.ScheduleStatus=='pending')
    {

      this.LoadMembership(1,this.pagesize,null,'',this.ScheduleStatus)
    }
    else
    {
      this.LoadMembership(1,this.pagesize,null)
    }
    //current member
   // this.member.currentmember.subscribe(res => this.currentMember = res)

    // If the user changes the sort order, reset back to the first page.
    //this.sort.sortChange.subscribe(() => this.paginator.pageIndex = 0);
    // if(this.ScheduleStatus!='null')
    // this.LoadMembership(1,this.pagesize,null,'',this.ScheduleStatus)
    // else
    this.LoadMembership(1,this.pagesize,null,'','','',this.filterids)
  }
  changeLablesPositions(id, event) {

    const _title: string = 'User Delete';
    const _description: string = 'Are you sure to permanently delete this user?';
    const _waitDesciption: string = 'member is updating...';
    const _updateMessage = `member status has been updated`;
    const _failedToUpdate = `sorry there is problem to update this member try again`
    const dialogRef = this.layoutUtilsService.deleteElement(_title, _description, _waitDesciption);
    dialogRef.afterClosed().subscribe(res => {
      if (!res) {
        return;
      }
      if (event['checked']) {
        this.member.updateMembership(id, { 'status': true }).subscribe(res => {
          if (res['result']) {
            this.layoutUtilsService.showActionNotification(_updateMessage, MessageType.Update);
          }
          else {
            this.layoutUtilsService.showActionNotification(_failedToUpdate, MessageType.Update);
          }
        })
      }
      else {
        this.member.updateMembership(id, { 'status': false }).subscribe(res => {
          if (res['result']) {
            this.layoutUtilsService.showActionNotification(_updateMessage, MessageType.Update);
          }
          else {
            this.layoutUtilsService.showActionNotification(_failedToUpdate, MessageType.Update);
          }
        })
      }
    })
  }


  onSearchClear()
    {
    this.SearchKey=""
    this.applyFilter()
  }

  /*
  *
  *
  */
  //method to  search online
  applyFilter(orderby=''){
    if(this.SearchKey && this.SearchKey.length > 0)
      var search = this.SearchKey.trim().toLowerCase();
    else var search='';
    if (this.filterStatus && this.filterStatus.length > 0)
      var status = this.filterStatus;
    else status = '';

    if (this.filterSchedual && this.filterSchedual.length > 0)
      var schedual = this.filterSchedual;
    else schedual = '';

    if (this.filterPayment && this.filterPayment.length > 0)
      var payment = this.filterPayment;
    else payment = '';

    this.LoadMembership(null,null,search,status,schedual,payment,this.filterids,this.orderby,this.order);
  }

  LoadMembership(page,perpage,search='',status='',schedual='',payment='',ids='',orderby='',order=-1) {
    merge(this.sort.sortChange, this.paginator.page)
      .pipe(
        startWith({}),
        switchMap(() => {
          this.isLoadingResults = true;
          if(this.pub_key)
            return this.member!.getMembership(this.pub_key, page,perpage, search,status,schedual,payment,ids,orderby,order);
        }),
        map(res => {
          console.log(res)
          // Flip flag to show that loading has finished.
          this.isLoadingResults = false;
          this.isRateLimitReached = false;
          this.resultsLength = res['data'].length;

          return res
        }),
        catchError(() => {
          this.isLoadingResults = false;

          this.isRateLimitReached = true;
          return observableOf([]);
        })
      ).subscribe(data => {
        if (data['result'])
          this.dataSource.data = data['data']
          this.length=data['data'].length
        this.changeDetectorRefs.detectChanges();
        console.log(data,this.length)

      });
  }
  //access data of Membership
  accessPackageData(membership) {
    this.member.getPackageBYId(membership['membership_id']).subscribe(res => {
      if (res['result'])
        this.member.DataOfPackage(res['data'][0])
    })
  }
  confirmRenew(row) {

      this.router.navigate(['/default/members/add',this.pub_key,'select'],{queryParams:{status:'renew',rp:row._id}})

  }

  nextPage(event) {
    console.log(event)
    if(typeof this.ScheduleStatus=='string'&&this.ScheduleStatus=='pending')
    {

      this.LoadMembership(event.pageIndex + 1, event.pageSize, null,'',this.ScheduleStatus)
    }
    else
    {
      this.LoadMembership(event.pageIndex + 1, event.pageSize, null)
    }

  }
  //search
  search = (text$: Observable<string>) =>
    text$.pipe(
      debounceTime(200),
      distinctUntilChanged(),
      map(term => { console.log(this.model) })
    );

}
