import { Component, ChangeDetectionStrategy, ViewChild, TemplateRef, OnInit } from '@angular/core';
import { startOfDay, endOfDay, subDays, addDays, endOfMonth, isSameDay, isSameMonth, addHours } from 'date-fns';
import { Subject } from 'rxjs';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { CalendarEvent, CalendarEventAction, CalendarEventTimesChangedEvent, CalendarView } from 'angular-calendar';
import { SchedualService } from '../../../../../../core/auth/_services';
import { MembersService } from '../../../members.service'
import { LayoutUtilsService, MessageType } from '../../../../../../core/_base/crud';

//route
import { Router, ActivatedRoute } from '@angular/router'
const colors: any = {
  red: {
    primary: '#bb2124',
    secondary: '#bb2124'
  },
  blue: {
    primary: '#1e90ff',
    secondary: '#D1E8FF'
  },
  yellow: {
    primary: '#e3bc08',
    secondary: '#FDF1BA'
  }
};

@Component({
  selector: 'kt-reschdual-andcancel',
  templateUrl: './reschdual-andcancel.component.html',
  styleUrls: ['./reschdual-andcancel.component.scss']
})
export class ReschdualAndcancelComponent implements OnInit {
  @ViewChild('modalContent') modalContent: TemplateRef<any>;
  view: CalendarView = CalendarView.Week;
  CalendarView = CalendarView;
  viewDate: Date = new Date();
  startDay: number = 1;
  modalData: {
    action: string;
    event: CalendarEvent;
  };
  selectedMembership;
  refresh: Subject<any> = new Subject();
  rescedualPackages;
  events: CalendarEvent[];
  activeDayIsOpen: boolean = true;
  selectedTimes: any;
  user_key;
  user_id;
  ReschdualPackage;
  membership_id;
  units_key;
 ReschedualMembershipData;
  constructor(private modal: NgbModal,
    private router: ActivatedRoute
    , private service: SchedualService, private Membershipservice: MembersService, private layoutUtilsService: LayoutUtilsService, private route: Router) {
    this.user_id = this.router.parent.snapshot.paramMap.get('id');
    this.membership_id = this.router.snapshot.paramMap.get('id');
    this.units_key = this.router.snapshot.queryParamMap.get('uk')
    this.Membershipservice._savedReschdualPackage.subscribe(res => {
      this.ReschdualPackage = res;
      this.ReschdualPackage = JSON.parse(this.ReschdualPackage);
      console.log(this.ReschdualPackage)
      if (!(this.ReschdualPackage.day)&&!this.ReschdualPackage.day)
        this.route.navigate(['/default/members/list/profile', this.user_id, 'reschedule'])
    })
    //reschedual package
    this.Membershipservice.currentSelectedMembership.subscribe(res => {
      this.rescedualPackages = res;
    })
    this.events = []; this.selectedTimes = {};
    this.user_id = this.router.parent.snapshot.paramMap.get('id');
    this.membership_id = this.router.snapshot.paramMap.get('id');
    this.units_key = this.router.snapshot.queryParamMap.get('uk')
   
    console.log(this.units_key, this.membership_id)
    if (this.units_key && this.membership_id)
      this.getSchedual(this.units_key, this.membership_id, new Date())
   //membership piD
   if(this.router.snapshot.queryParamMap.get('piD'))
   this.Membershipservice.getPackageBYId(this.router.snapshot.queryParamMap.get('piD')).subscribe(res=>{
      if(res['result'])
       this.ReschedualMembershipData=res['data'][0]

   })

  }
  ngOnInit() {
    let url = this.route.url.split('/')
    this.Membershipservice.changeUrl(url[url.length - 1])
  }
  handleEvent(action: string, event: CalendarEvent): void {
    var day = new Date(event.start).getDay();
    var from = new Date(event.start).getTime();
    var to = new Date(event.end).getTime();

    var selectcount = this.objectSize(this.selectedTimes);
    console.log('selected', day, from, to, selectcount, this.selectedTimes)
    if (event.color.primary == '#e3bc08') {
      delete this.selectedTimes[day];
      event.color = colors.blue;
    } else {
      if (this.selectedTimes[day] != undefined) {
        alert('You Select Time On this day Please unselect and select Again');
      } else {
        this.selectedTimes[day] = { from, to };
        event.color = { primary: '#e3bc08', secondary: '#CCC' };
      }
    }

  }
  /*
    /*
      get all schedual time
    */
  getSchedual(units_key, _package, startday) {
    console.log(units_key, _package, startday)
    this.startDay = startday.getDay();
    this.viewDate = startday;
    var start_date = startday.getTime();
    this.service.getSchedual({ units_key, package: _package, start_date }).subscribe(
      res => {
        console.log('resut', res)
        if (res['result']) {
          this.drawOnCalender(res.data);
         // console.log(this.rescedualPackages)
         // this.drawOnCalender(this.rescedualPackages)
        }
      }
    )
  }

  /*
    get Activity By Id
  */
  getActivity(_package) {
    this.service.getActivity(_package).subscribe(
      res => {
        console.log('resut', res)
        if (res['result']) {
          this.drawOnCalender(res.data.schedual_time);
        }
      }
    )
  }

  SaveAttendance() {
    var times = new Array();
    Object.values(this.selectedTimes).forEach(async function (item) {
      times.push(item);
    });
    //range of reservation
  let reservationRange=this.ReschdualPackage.number;
    if(reservationRange>this.ReschedualMembershipData['day_per_week'])
     reservationRange=this.ReschedualMembershipData['day_per_week']
 console.log(reservationRange,this.ReschedualMembershipData['day_per_week'])
    if(times.length!=reservationRange)
    {
    alert('You should select '+reservationRange+ 'times')
    }
    else{
    var data;
  console.log(this.ReschdualPackage.endoldday)
  if( this.rescedualPackages['_idAttendance'])
   data = { status:'active',units_key: this.units_key, membership_id: this.membership_id, reservation: times, user_key: this.user_id, startoldday: this.ReschdualPackage.day, endoldday: this.ReschdualPackage.endoldday ,_idAttendance:this.rescedualPackages['_idAttendance']}
  else
   data = { status:'active',units_key: this.units_key, membership_id: this.membership_id, reservation: times, user_key: this.user_id, startoldday: this.ReschdualPackage.day, endoldday: this.ReschdualPackage.endoldday }
    this.Membershipservice.reschedualAttendance(data).subscribe(
      res => {
        if (res['result']) {
          this.layoutUtilsService.showActionNotification("reservation has sucessfully added", MessageType.Create, 5000, true, true);
          let member_id=this.route.url.split('/')[5]
          this.route.navigate(['/default/members/list/profile',member_id,'reschedule'])
        } else alert(res['error'])
      }
    )
  }
}
  drawOnCalender(data, activity = false) {
    this.refresh.next();
    this.events = [];
    for (let i = 0; i < data.length; i++) {
      if (activity) {
        var start = new Date(data[i].start);
        var end = new Date(data[i].end);
      } else {
        var start = new Date(data[i].from);
        var end = new Date(data[i].to);
      }
      var title = this.gethoursandminutes(start) + " to " + this.gethoursandminutes(end);
      var color = colors.blue;
      //if(i%2) var color = colors.blue;else var color = colors.yellow;
      this.events.push({ start, end, title, color });
    }
  }

  deleteEvent(eventToDelete: CalendarEvent) {
    this.events = this.events.filter(event => event !== eventToDelete);
  }

  setView(view: CalendarView) {
    this.view = view;
  }

  closeOpenMonthViewDay() {
    this.activeDayIsOpen = false;
  }

  gethoursandminutes(date) {
    return ("0" + date.getHours()).slice(-2) + ":" + ("0" + date.getMinutes()).slice(-2);
  }

  objectSize(obj) {
    var size = 0, key;
    for (key in obj) {
      if (obj.hasOwnProperty(key)) size++;
    }
    return size;
  };
}
