import { Injectable, assertPlatform } from '@angular/core';
import { HttpClient } from '@angular/common/http';

// Environment
import { PublicMethods } from '../../_public/-public-methods'
import { Observable } from 'rxjs';

const _url = PublicMethods.createURL


@Injectable({
    providedIn: 'root'
})



export class WorkOutService {
    constructor(private http: HttpClient) { }
    getWorkOuts(club_key): Observable<any>{
        var parent_keys = (JSON.parse(localStorage.getItem('user')))['lastloginaccount'];
        var public_keys = (JSON.parse(localStorage.getItem('user')))['pub_key'];
        let WORKOUT_URL=_url('workouts','getclubworkoutGrouped','',public_keys,parent_keys,1,30);
        return this.http.post<any>(WORKOUT_URL,{club_key})
    }
    
}