import {ActivatedRoute,Router} from '@angular/router'
import { CategoriesComponent } from './categories/categories.component';
const colors: any = {
  red: {
    primary: '#CD5C5C',
    secondary: '#CD5C5C'
  },
  blue: {
    primary: '#1e90ff',
    secondary: '#D1E8FF'
  },
  yellow: {
    primary: '#e3bc08',
    secondary: '#FDF1BA'
  },
  green: {
    primary: '#D0F0C0',
    secondary: '#D0F0C0'
  }
};
@Component({
  selector: 'kt-workout',
  templateUrl: './workout.component.html',
  styleUrls: ['./workout.component.scss']
})
export class WorkoutComponent  {
  @ViewChild('modalContent') modalContent: TemplateRef<any>;
  view: CalendarView = CalendarView.Week;
  CalendarView = CalendarView;
  viewDate: Date = new Date();
  startDay: number = 1;
  modalData: {
    action: string;
    event: CalendarEvent;
  };

  data;
  refresh: Subject<any> = new Subject();

  events: CalendarEvent[];
  activeDayIsOpen: boolean = true;
  selectedTimes: any;
  options: string = '';
  membership_id: string = '';
  units_key: string = '';
  start_date: Date = new Date();
  user_key: string = '';
  //??
  trainer_key: string = '';

  constructor(public dialog: MatDialog,
    private modal: NgbModal,
    private service: SchedualService,
    private memberService: MembersService,
    private router:Router,
    private route:ActivatedRoute) {
    this.events = []; this.selectedTimes = {};

    this.user_key =(this.router.url.split('/'))[5]

    // this.memberService.currentmember.subscribe(res => {
    //   console.log(res); if (res) this.user_key = res['pub_key']
    //   console.log(this.user_key)
    let status=this.route.snapshot.queryParamMap.get('status');
    if(status=='pe')
    {
     status='active'
    }
    if(status=='co')
    {
      status='complete'
    }
      if (this.user_key)
      {
        console.log(this.user_key)
        this.viewAttendance(this.user_key, this.trainer_key,'','',status);
      }
    // })


  }


  handleEvent(action: string, event: CalendarEvent,eventCell): void {

    // this.modalData = { event,() action };
    // this.modal.open(this.modalContent, { size: 'lg' });
    let status:String;
     status= (JSON.parse(event['id'].toString()))['status']
    console.log(status)
    if(status.includes("active")||status.includes("pending-payment"))
    {
      console.log(status)
    this.memberService.saveReschdualPackage(event['id'])
    const dialogRef = this.dialog.open(CategoriesComponent, {
       data:event['id'], 
      });
    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');
      
      this.data = result;
    });
    }else
    {

    }
  }
  /*
    get all schedual time
  */
  viewAttendance(user_key, trainer_key, _package, startday,status) {
    this.service.viewAttendance({ user_key, trainer_key, package: _package, startday ,status}).subscribe(
      res => {
        console.log('all times', res);
        if (res['result']) {
          this.refresh.next();
          this.events = [];
          console.log(res.data);
          for (let i = 0; i < res.data.length; i++) {
            var start = this.get_UTC_timestamp(res.data[i].from);
            var end =   this.get_UTC_timestamp(res.data[i].to);
            var title = "name: " + "\n time:" + this.gethoursandminutes(start) + " to " + this.gethoursandminutes(end) + " "+'workout-number : 0';
            var color = colors.blue;
  
            var id = `{"day":"${res.data[i].day}","status":"${res.data[i].status}","branch_key":"${res.data[i].branch_key}","club_key":"${res.data[i].club_key}","package_id":"${res.data[i].membership_id}","membership_id":"${res.data[i].membershipandpayment_id}","trainer_key":"${res.data[i].trainer_key}","user_key":"${user_key}","units_key":"${res.data[i].units_key}","from":"${res.data[i].from}","to":"${res.data[i].to}","_idAttendance":"${res.data[i]._id}"}`
            if(res.data[i].status=='active') color = colors.green;
            if (res.data[i].status == "reschedual") color = colors.yellow;
            if (res.data[i].status == "cancel") color = colors.red;
            //if(i%2) var color = colors.blue;else var color = colors.yellow;
            this.events.push({ start, end, title, color, id });
          }
        }
      }
    )
  }

  SaveAttendance() {
    var times = new Array();
    Object.values(this.selectedTimes).forEach(async function (item) {
      times.push(item);
    });


    var data = { units_key: this.units_key, package: this.membership_id, start_date: this.start_date, recervation: times }
    this.service.addAttendance(data).subscribe(
      res => {

        if (res['result']) {
          // action after recervation
        }
      }
    )
  }

  deleteEvent(eventToDelete: CalendarEvent) {
    this.events = this.events.filter(event => event !== eventToDelete);
  }

  setView(view: CalendarView) {
    this.view = view;
  }

  closeOpenMonthViewDay() {
    this.activeDayIsOpen = false;
  }

  changeOptions(option) {
    this.options = option;
    console.log(this.options, option);
  }

  gethoursandminutes(date) {
    var hours = date.getHours();
    var minutes = date.getMinutes();
    var ampm = hours >= 12 ? 'pm' : 'am';
    hours = hours % 12;
    hours = hours ? hours : 12; // the hour '0' should be '12'
    minutes = minutes < 10 ? '0' + minutes : minutes;
    var strTime = hours + ':' + minutes + ' ' + ampm;
    return strTime;
  }

  get_UTC_timestamp(date){
     var d1 = new Date(date);
     var d2 = Date.UTC( d1.getUTCFullYear(), d1.getUTCMonth(), d1.getUTCDate(), d1.getUTCHours(), d1.getUTCMinutes(), d1.getUTCSeconds() );
     var utc_timestamp = new Date(d2);
     return utc_timestamp;
   }

  objectSize(obj) {
    var size = 0, key;
    for (key in obj) {
      if (obj.hasOwnProperty(key)) size++;
    }
    return size;
  };
  
  

}
