import { Component, OnInit ,ChangeDetectorRef} from '@angular/core';
import { MembersService } from '../../members.service'
import {MatPaginator, MatSort, MatTableDataSource} from '@angular/material';
import {MatDialog, MAT_DIALOG_DATA} from '@angular/material';
import {ReceiptDialogComponent} from './receipt-dialog/receipt-dialog.component'
import {ActivatedRoute,Router} from '@angular/router'
@Component({
  selector: 'kt-payments-member',
  templateUrl: './payments-member.component.html',
  styleUrls: ['./payments-member.component.scss']
})
export class PaymentsMemberComponent implements OnInit {
  SearchKey:string='';
  filterStatus: string = '';
  filterPayment: string = '';
  filterSchedual: string = '';
  orderby: string = '';
  order : number = -1;

  member;
  membershipList;
  displayedColumns = ['increment','package_name', 'price','payment_date','payment_method','payment_status','Action'];
  exampleDatabase: MembersService| null;
  dataSource = new MatTableDataSource();
  getallpayment = [{key:'Compelete',value:'complete'},{key:'Pending',value:'pending'}]
  oderitems = [{key:'Latest',value:'id'},{key:'Name',value:'name'},{key:'Price',value:'price'},{key:'Payment Date',value:'payment_date'}
            ,{key:'Payment method',value:'payment_method'},{key:'Payment status',value:'payment_status'}]
  pub_key
  statusPayment:string=''
  constructor(private memberservice: MembersService,
    private changeDetectRef:ChangeDetectorRef,
    private dialog: MatDialog,
    private router:Router,
    private route:ActivatedRoute) {
      this.pub_key =(this.router.url.split('/'))[5]
      console.log(this.pub_key)
    this.statusPayment=this.route.snapshot.queryParamMap.get('status')
   }

  ngOnInit() {
    // this.memberservice.currentmember.subscribe(res => {
    //   this.member = res
    // })
    if(typeof this.statusPayment=='string')
    this.loadPayment(null,null,'',status,'',this.statusPayment,this.orderby,this.order)
    else
    this.loadPayment(null,null,'',status,'','',this.orderby,this.order)
  }

  previewInvoice(membership)
  {

      let dialogRef = this.dialog.open(ReceiptDialogComponent, {
        data: { membership },    autoFocus: false,
        panelClass: 'trend-dialog',
         height: window.innerHeight+'px',
      });

      dialogRef.afterClosed().subscribe(result => {
        console.log('The dialog was closed');
      });

  }

  onSearchClear()
    {
    this.SearchKey=""
    this.applyFilter()
  }

  /*
  *
  *
  */
  //method to  search online
  applyFilter(orderby=''){
    if(this.SearchKey && this.SearchKey.length > 0)
      var search = this.SearchKey.trim().toLowerCase();
    else var search='';
    if (this.filterStatus && this.filterStatus.length > 0)
      var status = this.filterStatus;

      else status = '';

    if (this.filterSchedual && this.filterSchedual.length > 0)
      var schedual = this.filterSchedual;
    else schedual = '';

    if (this.filterPayment && this.filterPayment.length > 0)
      var payment = this.filterPayment;
    else payment = '';

    this.loadPayment(null,null,search,status,schedual,payment,this.orderby,this.order);
  }

  loadPayment(pages = 1, perpage = 10, search = null,status='',schedual='',payment='',orderby='',order=-1) {
    if (this.pub_key) {
      this.memberservice.getMembership(this.pub_key,pages,perpage,search,status,schedual,payment,'',orderby,order).subscribe(res=>{
        console.log(res['data'])
        if(res['result'])
        {
          this.dataSource.data=res['data']
          console.log(res['data'])
          this.changeDetectRef.detectChanges();
        }
      })
    }
  }

  nextPage(event,serviclo='') {
    console.log(event)
    if(typeof this.statusPayment=='string')
    this.loadPayment(event.pageIndex + 1, event.pageSize, null,status,'',this.statusPayment,this.orderby,this.order)
    else
    this.loadPayment(event.pageIndex + 1, event.pageSize, null,status,'','',this.orderby,this.order)

  }
}
